<template>

   <div class="desPage">
         <header class="top-hd">
        <div class="in-hd">
            <a href="javascript:;" class="back-btn" v-link="{path:'/index'}"></a>
            <h1 class="title">商品描述</h1>
         </div>
    </header>
    <section class="main-content" style="background:#fff;font-size:28px">
        <div class="inner-content">
           <div id="editCon" class="editCon">
               <input type="hidden" id="target">
                <div class="article-content" &click="inputCon" id="articleContent">
                    
                </div>
           </div>     
        </div>
    </section>
    <footer class="footer">
        <div class="cross2Btn">
            
            <a href="javascript:;" class="btnPub btn-first" style="position:relative">添加图片<input class="imgFile" type="file" accept="image/*" style="position:absolute;left:0;top:0;width:344px;height:72px;overflow:hidden;opacity:0" id="imageUpload"></a>
            <a href="javascript:;" class="btnPub btn-two" id="confirmBtn" @click="getValue">确认提交</a>
        </div>
    </footer>
    </div>



</template>


<script>
     


require('../artEditor.js');

    export default{
        
       
    
        data(){
        
           return {
           
                value:""
           }  
        },
        
        methods:{
            
            
        
            getValue:function(){
                
                this.$nextTick(function () {
                    // DOM 现在更新了
                    // `this` 绑定到当前实例

                    var value = $('#articleContent').getValue();
                    
                    this.value = value;
                    
                    sessionStorage.setItem("des",value);
                    
                    var bb = value.replace(/<[^>]+>/g,"");
                    
                    router.go({
                    
                        name:"index",
                        
                        query:{
                        
                            des:bb
                        }
                    });
                    
                  })

            }
           
        },
        
        ready:function(){
            
        
            this.$nextTick(function(){
                
                $('#articleContent').artEditor({
                    imgTar: '#imageUpload',
                    limitSize: 5,   // 兆
                    showServer: false,
                    uploadUrl: '',
                    data: {},
                    uploadField: 'image',
                    placeholader: '<p>请输入文章正文内容</p>',
                    validHtml: ["br"],
                    uploadSuccess: function(res) {
                        // return img url
                        return res.path;
                    },
                    uploadError: function(res) {

                    }
            })
            })
        },
        
        
         components:{
            
            //autocomplete
        }
    }
        

            
</script>



